/* #f5da6a */
@import url(https://fonts.googleapis.com/css?family=Raleway:400,100,200,300,500,600,700,800,900);
/* Main */
html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, main, menu, nav, section, summary {
  display: block;
}

audio, canvas, progress, video {
  display: inline-block;
  vertical-align: baseline;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden], template {
  display: none;
}

a {
  background-color: transparent;
}

a:active, a:hover {
  outline: 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b, strong {
  font-weight: bold;
}

dfn {
  font-style: italic;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

mark {
  background: #ffff00;
  color: #000000;
}

small {
  font-size: 80%;
}

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
}

pre {
  overflow: auto;
}

code, kbd, pre, samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

button, input, optgroup, select, textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

button {
  overflow: visible;
  text-transform: none;
}

select {
  text-transform: none;
}

button, html input[type="button"], input[type="reset"], input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled], html input[disabled] {
  cursor: default;
}

button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input {
  line-height: normal;
}

input[type="checkbox"], input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="number"]::-webkit-inner-spin-button, input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: content-box;
}

input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  border: 0;
  padding: 0;
}

textarea {
  overflow: auto;
}

optgroup {
  font-weight: bold;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td, th {
  padding: 0;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-size: 1.8rem;
  font-family: Raleway,Helvetica,Arial,sans-serif;
  line-height: 160%;
  font-weight: 200;
  color: #312a10;
  background-color: #f0eff4;
}

.container {
  width: 80%;
  margin: 0 auto;
}

h1, h2, h3, h4, h5, h6 {
  color: #bfc406;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
}

a {
  text-decoration: none;
  font-weight: 700;
  color: #d14348;
}

a:hover {
  border-bottom: 2px solid #d14348;
}

em {
  font-weight: 700;
  color: #f3d350;
  font-size: 140%;
  font-style: normal;
}

/* Modules */
.header {
  background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.5)), url("../images/running.jpg");
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.5)), url("../images/running.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  height: 100vh;
  max-height: 500px;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  text-align: center;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
}

@media screen and (min-width: 600px) {
  .header {
    background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.5)), url("../images/running.jpg");
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.5)), url("../images/running.jpg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    height: 100vh;
    max-height: 800px;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
  }
}

.tagline {
  display: none;
}

@media screen and (min-width: 600px) {
  .tagline {
    display: block;
    color: #eee8d6;
    max-width: 40em;
    line-height: 140%;
    font-size: 25px;
    font-size: 2.5rem;
  }
  .tagline h1 {
    display: none;
  }
  .tagline em {
    color: #f3d350;
    font-size: 140%;
    font-style: normal;
  }
}

.nav {
  margin-top: 1em;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  /* nav-content */
}

.nav .nav-elements {
  /* navbar */
  /* branding */
}

.nav .nav-elements .branding {
  margin: 0 auto;
  max-width: 180px;
}

.nav .nav-elements .navbar {
  list-style-type: none;
  margin: 0;
  padding: 0;
  line-height: 100%;
}

.nav .nav-elements .navbar a {
  font-weight: 600;
  color: #eee8d6;
  text-decoration: none;
  display: block;
  padding: 0.3em 15px;
  -webkit-transition: 1s ease-out;
  transition: 1s ease-out;
}

.nav .nav-elements .navbar a:after {
  content: '';
  display: block;
  height: 5px;
  margin-top: 2px;
  width: 0;
  -webkit-transition: width .3s ease-out, background-color .3s ease-out;
  transition: width .3s ease-out, background-color .3s ease-out;
}

.nav .nav-elements .navbar a:hover {
  color: #f3d350;
  border-bottom: none;
}

.nav .nav-elements .navbar a:hover:after {
  width: 100%;
  background: #f3d350;
}

@media screen and (min-width: 600px) {
  .nav .nav-elements {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: start;
    -webkit-align-items: flex-start;
    -ms-flex-align: start;
    align-items: flex-start;
  }
}

@media screen and (min-width: 600px) {
  .nav .nav-elements .branding {
    width: 30%;
    margin: 0;
  }
}

@media screen and (min-width: 600px) {
  .nav .nav-elements .navbar {
    width: 70%;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-pack: end;
    -webkit-justify-content: flex-end;
    -ms-flex-pack: end;
    justify-content: flex-end;
  }
}

.section {
  min-height: 40vh;
  padding: 2% 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  line-height: 30px;
  line-height: 3rem;
  font-size: 20px;
  font-size: 2rem;
}

.section .container {
  width: 80%;
  max-width: 1200px;
}

.section .headline {
  text-transform: uppercase;
  font-weight: 300;
  font-size: 40px;
  font-size: 4rem;
  margin-top: 30px;
  margin-top: 3rem;
  line-height: 110%;
}

.section em, .section a {
  font-size: 28px;
  font-size: 2.8rem;
  vertical-align: -6%;
}

.footer {
  background: #312a10;
  color: #eee8d6;
  padding: 10px 0px 50px 0px;
  padding: 1rem 0rem 5rem 0rem;
  line-height: 100%;
  text-align: center;
}

.footer .nav {
  margin-top: 0;
}

.footer .nav-elements {
  position: relative;
}

.footer .legal {
  max-width: 700px;
  margin: 0 auto;
  font-size: 10px;
  font-size: 1rem;
  line-height: 110%;
  margin-top: 30px;
  margin-top: 3rem;
}

.footer .social {
  margin: 0 auto;
  padding: 15px 0 0 15px;
  overflow: hidden;
  text-align: center;
}

.social li {
  display: inline-block;
  width: 50px;
  list-style-type: none;
  margin: 0 15px 0 0;
}

.social a {
  display: block;
  width: 50px;
  height: 50px;
  text-indent: -9999px;
  overflow: hidden;
  border-width: 0;
}

.social .twitter {
  background-image: url("../images/social/twitter.png");
}

.social .facebook {
  background-image: url("../images/social/facebook.png");
}

.social .instagram {
  background-image: url("../images/social/instagram.png");
}

@media screen and (min-width: 600px) {
  .footer .social {
    position: absolute;
    right: 0;
    bottom: 0;
    padding: 0 0 15px;
  }
}

/* Content */
#products .product-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-flow: row wrap;
  -ms-flex-flow: row wrap;
  flex-flow: row wrap;
}

#products .product-list .product-item {
  width: 50%;
  line-height: 150px;
  color: #ffffff;
  font-weight: bold;
  font-size: 1em;
  text-align: center;
}

@media screen and (min-width: 600px) {
  #products .product-list .product-item {
    width: 33.3333333334%;
  }
}

@media screen and (min-width: 900px) {
  #products .product-list .product-item {
    width: 25%;
  }
}

@media screen and (min-width: 1200px) {
  #products .product-list .product-item {
    width: 16.666667%;
  }
}

#history {
  background: #f3d350;
}

#history .headline {
  color: #d14348;
}

#history em {
  color: #e69f2a;
}

#guarantee {
  color: #ffffff;
  font-size: 20px;
  font-size: 2rem;
  background: #acb005;
}

#guarantee .headline {
  color: #f5dc73;
}

#guarantee a {
  color: #d2d651;
}

#people {
  background: #f5dc73;
}

#people .headline {
  color: #ffffff;
  padding-bottom: 20px;
  padding-bottom: 2rem;
}

#people a {
  color: #acb005;
}

#people .people-cards .person-card {
  -webkit-transition: all .2s ease-in;
  transition: all .2s ease-in;
  -webkit-transform: scale(0.9);
  -ms-transform: scale(0.9);
  transform: scale(0.9);
  opacity: .8;
  margin-bottom: 5%;
  max-width: 400px;
  background: #ffffff;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 5px 5px 3px rgba(243, 211, 80, 0.5);
  border-radius: 20px;
}

#people .people-cards .person-card:hover {
  -webkit-transform: scale(1.05);
  -ms-transform: scale(1.05);
  transform: scale(1.05);
  opacity: 1;
}

#people .people-cards .person-card .card-info {
  background: #ffb12f;
  background: rgba(255, 177, 47, 0.9);
  padding: 3% 7%;
}

#people .people-cards .person-card .card-info .card-name {
  font-size: 20px;
  font-size: 2rem;
  line-height: 110%;
  color: #d14348;
}

#people .people-cards .person-card .card-info .card-title {
  color: #ffffff;
  color: rgba(255, 255, 255, 0.6);
  font-size: 14px;
  font-size: 1.4rem;
  line-height: 100%;
}

#people .people-cards .person-card .card-text {
  font-size: 16px;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 130%;
  margin: 0;
  padding: 7%;
}

#people .people-cards .person-card .card-text em {
  color: #f2a82d;
  font-size: 22px;
  font-size: 2.2rem;
}

@media screen and (min-width: 600px) {
  #people .people-cards {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between;
  }
}

@media screen and (min-width: 600px) {
  #people .people-cards .person-card {
    width: 45%;
  }
}

@media screen and (min-width: 1200px) {
  #people .people-cards .person-card {
    width: 30%;
  }
}

.modal-supported #products .product-item img {
  cursor: pointer;
}

#modal, #modal .backdrop {
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}

#modal {
  z-index: 1000;
  position: fixed;
}

#modal .backdrop {
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
  position: absolute;
}

#modal img {
  z-index: 1001;
  position: relative;
  max-height: 80%;
  max-width: 80%;
  margin: 5% auto 0;
}

#modal button {
  z-index: 1100;
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 10px 25px;
}
